import React, { Component } from 'react';
import GoogleAuthentication from '../GoogleAuthentication'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"; //fontaweresome
import { faTwitter } from "@fortawesome/free-brands-svg-icons"; //twitter
import { faGithub } from "@fortawesome/free-brands-svg-icons"; //github
import { faLine } from "@fortawesome/free-brands-svg-icons"; //line


import '../animation.scss';

const twitterIcon = {
    fontSize:"1.5em",
    color:"white",
    cursor: "pointer"
}
const lineIcon = {
    fontSize:"1.5em",
    color:"#Line",
    cursor: "pointer"
}

let nTimer;
export default class Login extends Component{
    constructor(props){
        super(props);
        this.state={
            isChange:false,
            isLoginfails:false
        }
    }

    login(user,sns){
        this.setState({isChange:true});
        setTimeout(() => {
            this.props.action.userSignin(user,sns);
        }, 1000)
        nTimer = setTimeout(() => {
            this.setState({isLoginfails:true});
        }, 20000)
        
    }

    render(){
        if(this.props.user.session.length > 0 && this.state.isChange){
            setTimeout(() => {
                this.setState({isChange:false});
                if(nTimer){clearTimeout(nTimer);}
            }, 1000)
        }
        if(this.props.user.session.length === 0){
        return(
            <div className={this.props.isPopup ? 'popup popup_effect' : 'popup popup_effect_de'} >
                 <div className="popup_wrap" ></div>
                 <div className="logwhir">
                     <div className="her-right">
                            {this.state.isChange ?
                                <div className="loadingContainer">
                                    {!this.state.isLoginfails ?
                                        <>
                                            <p>Google Account...</p>
                                            <div class="loader">Loading...</div>
                                        </>
                                    :
                                        <div className="loginerrorContainer">
                                            <p style={{paddingBottom:"20px"}}></p>
                                            <GoogleAuthentication action={(user,sns) => this.login(user,sns)}/>
                                            <div className="loginerrorTips">
                                                <p style={{paddingTop:"20px"}}></p>
                                                <p><a></a></p>
                                            </div>
                                            
                                        </div>
                                    }
                                </div>
                            :
                                <>
                                    <h2 className="flex-jus-center">N:urture</h2>
                                    <p className="clx"></p>
                                    <GoogleAuthentication action={(user,sns) => this.login(user,sns)}/>
                                    <p className="ghi hrm"><span></span></p>
                                    <a className="linkBox-twitter hrm" href=""><FontAwesomeIcon style={twitterIcon} icon={faTwitter} /> twitter</a>
                                    <a className="linkBox-line hrm" href=""><FontAwesomeIcon style={lineIcon} icon={faLine} /> Line</a>
                                    <div className="new_user" >
                                        <p className="cls">N:urture</p>
                                    </div>
                                </>
                            }
                     </div>
                 </div>
             </div>
        )
        }else{
            return(<></>)
        }
    }
}
